import React from 'react';

export const NavigationBar: React.FC = () => {
  return (
    <nav className="navbar">
      <button className="bg-blue-400 text-white px-2 p-2 py-1 rounded" onClick={() => window.location.href = '/AddStudent'}>Add Student</button>
      <button className="bg-blue-400 text-white px-2 p-2 py-1 rounded" onClick={() => window.location.href = '/AllStudent'}>All Students</button>
      <button className="bg-blue-400 text-white px-2 p-2 py-1 rounded" onClick={() => window.location.href = '/SearchStudent'}>Search Student</button>
    </nav>
  );
};

export default NavigationBar; // This is the default export